.flare {
    position: absolute;
    top: -50px;
    /* Adjust the vertical position as needed */
    left: -50px;
    /* Adjust the horizontal position as needed */
    width: 200px;
    /* Adjust the size of the flare as needed */
    height: 200px;
    /* Adjust the size of the flare as needed */
    background: radial-gradient(circle, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 70%);
    opacity: 0;
    -webkit-animation: flareAnimation 1s linear forwards;
            animation: flareAnimation 1s linear forwards;
}

@-webkit-keyframes flareAnimation {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0;
    }
}

@keyframes flareAnimation {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
        opacity: 1;
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 0;
    }
}
